import React, { Component } from "react";
import { connect } from "react-redux";
import ArrayLine from "../ArrayLine";
import AvarageRow from "../AvarageRow";
import styles from "./Array.module.css";
import * as selectors from "../../redux/selectors";

class Array extends Component {
  state = { id: "" };

  illuminateOnHandler = e => {
    const { id } = e.target;
    if (id.slice(0, 6) !== "number") {
      return;
    }
    this.setState({ id });
  };

  illuminateOffHandler = e => {
    const { id } = e.target;
    if (id.slice(0, 6) !== "number") {
      return;
    }
    this.setState({ id: "" });
  };

  render() {
    const { array } = this.props;

    return (
      <div>
        <table
          className={styles.matrix}
          onMouseOver={this.illuminateOnHandler}
          onMouseOut={this.illuminateOffHandler}
        >
          <tbody>
            {array.map(line => (
              <ArrayLine
                key={line.id}
                id={line.id}
                line={line.cells}
                hoverId={this.state.id}
              />
            ))}
            <AvarageRow />
          </tbody>
        </table>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    array: selectors.getArray(state)
  };
};

export default connect(mapStateToProps)(Array);
